import axios from 'axios';

const getRandomDog = () => {
  const config = {
    method: 'GET',
    url: 'https://api.thedogapi.com/v1/images/search',
    crossdomain: true,
    headers: {
      'Content-Type': 'application/jason',
      'x-api-key': 'aa9586b1-cc04-4269-a66f-83de96512de9'
    }
  };
  //console.log(config.url);
  return axios(config);
};

export { getRandomDog };
