import React from 'react';
import logo from './logo.svg';
import './App.css';
import * as dogService from '../src/services/dogService';

class App extends React.Component {
  state = {
    dogImage: {
      height: '',
      width: '',
      url: ''
    }
  };

  componentDidMount() {
    dogService
      .getRandomDog()
      .then(this.onGetRandomDogSuccess)
      .catch(this.onGetRandomDogError);
  }

  onGetRandomDogSuccess = aDog => {
    let dog = aDog.data[0];

    this.setState({
      dogImage: {
        height: dog.height,
        width: dog.width,
        url: dog.url
      }
    });

    console.log(dog);
  };

  onGetRandomDogError = er => {
    console.log(er);
  };

  render() {
    return (
      <div className='App'>
        <header className='App-header'>
          <img
            src={this.state.dogImage.url}
            style={{
              height: `${this.state.dogImage.height}`,
              width: `${this.state.dogImage.width}`
            }}
            className='App-logo'
            alt='logo'
          />
          <p>Dogs are Awesome</p>
          <a
            className='App-link'
            href='https://reactjs.org'
            target='_blank'
            rel='noopener noreferrer'>
            Learn React
          </a>
        </header>
      </div>
    );
  }
}
export default App;
